import React from 'react';
import { render } from 'react-dom';

export default class Page extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            text: "Welcome to my Page!",
            count: 0,
            colour: ""
            }
        this.handleClick = this.handleClick.bind(this);
        this.colourChange = this.colourChange.bind(this);
        }

    handleClick() {
        this.setState({
            count: this.state.count + 1,
        })
    }

    colourChange() {
        this.setState({
            colour: Math.floor(Math.random() * 65535).toString(16)
        })
    }
    

    render() {
        return(
            <div>
                <h1>{this.state.text}</h1>
                <br />
                <p>This is a test component</p>
                <button style={{background: "#"+this.state.colour}}>Count: {this.state.count}</button>
                <br />
                <button onClick={this.handleClick}>++ Increment Count</button>
                <br />
                <p>Testing color generator: {this.state.colour}</p>
                <button onClick={this.colourChange}>Change Colour</button>
            </div>
        );
    }
}